<template>
  <div>
    <hy-table
      :dataList="userList"
      :dataCount="userCount"
      @selectionChange="handleSelection"
      v-bind="contentTableConfig"
      v-model:page="pageInfo"
    >
      <template #hhandler>
        <el-button type="primary" @click="handleNewClick">新建用户</el-button>
      </template>
      <template #status="scope">
        <el-button size="min" :type="scope.row.enable ? 'success' : 'danger'">{{
          scope.row.enable ? '启用' : '禁用'
        }}</el-button>
      </template>
      <template #createAt="scope">
        <strong>{{ $filters.formatTime(scope.row.createAt) }}</strong>
      </template>
      <template #handler="scope">
        <div class="handle-btns">
          <el-button
            icon="el-icon-edit"
            size="mini"
            @click="handleEditClick(scope.row)"
            >编辑</el-button
          >
          <el-button
            icon="el-icon-delete"
            size="mini"
            type="danger"
            @click="handleDeleteClick(scope.row)"
            >删除</el-button
          >
        </div>
      </template>

      <template
        v-for="item in otherPropSlots"
        :key="item.prop"
        #[item.slotName]="scope"
      >
        <template v-if="item.slotName">
          <slot :name="item.slotName" :row="scope.row"></slot>
        </template>
      </template>
    </hy-table>
  </div>
</template>

<script>
import HyTable from '@/base-ui/table'
import { computed, defineComponent, watch, ref } from '@vue/runtime-core'
import { usePermission } from '@/hook/use-permission'
import { useStore } from '@/store'

export default defineComponent({
  components: {
    HyTable
  },
  props: {
    pageName: {
      type: String,
      required: true
    },
    contentTableConfig: {
      required: true
    }
  },
  emits: ['newBtnClick', 'editBtnClick'],
  setup(props, { emit }) {
    const store = useStore()
    store.dispatch('system/getPageListAction', {
      pageName: props.pageName,
      queryInfo: {
        offset: 0,
        size: 10
      }
    })
    const isCreate = usePermission(props.pageName, 'create')
    const isUpdate = usePermission(props.pageName, 'update')
    const isDelete = usePermission(props.pageName, 'delete')
    const isQuery = usePermission(props.pageName, 'query')
    // console.log(isCreate, isUpdate, isDelete, isQuery)
    // 1.双向绑定pageInfo
    const pageInfo = ref({ currentPage: 0, pageSize: 10 })
    watch(pageInfo, () => getPageData())

    const getPageData = (queryInfo) => {
      if (!isQuery) return
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: pageInfo.value.currentPage * pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      })
    }
    getPageData()

    const userList = computed(() =>
      store.getters['system/pageListData'](props.pageName)
    )
    const userCount = computed(() =>
      store.getters['system/pageCountData'](props.pageName)
    )

    const otherPropSlots = props.contentTableConfig?.propList.filter((item) => {
      if (item.slotName === 'status') return false
      if (item.slotName === 'createAt') return false
      if (item.slotName === 'updateAt') return false
      if (item.slotName === 'handler') return false
      // if (item.slotName === 'image') return false
      return true
    })

    const handleDeleteClick = (item) => {
      console.log(item)
      store.dispatch('system/deletePageDataAction', {
        pageName: props.pageName,
        id: item.id
      })
    }

    const handleNewClick = () => {
      emit('newBtnClick')
    }
    const handleEditClick = (item) => {
      // console.log(item)
      emit('editBtnClick', item)
    }
    const handleSelection = () => {
      console.log('处理选项')
    }
    return {
      userList,
      getPageData,
      userCount,
      pageInfo,
      otherPropSlots,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    }
  }
})
</script>

<style lang="less" scoped>
.handle-btns {
  display: flex;
}
</style>
